  display: grid;
  justify-content: center;
  line-height: 90px;
  border-radius:10px;

}
.add--stock{
  width:40px;
  /* margin-left: 20px; */
  height: 30px;
  display: grid;
  justify-content: center;
  line-height: 30px;
  border-radius:10px;
  border: 1px solid var(--DARKBLUE);

}

.backdrop{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
}
