  color: var(--BLACK);  
  margin-right: 10px;
  margin-bottom:20px;
  margin-left:50px;
  
}
.modal--title--left{
  display: flex;
  flex-direction: row;    
  align-items: center;
  gap:20px;
  border-bottom: 1px solid var(--LIGHTBLUE);
}
.modal--title--right{
  display: flex;
  flex-direction: row;    
  align-items: center;

}

.tficlose{
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal--chart--group{
  display: flex;
}
.modal--chart{
